<template>
  <div class="home">
    <div v-if="!quote || !quote.text">
      <div class="quote-text-container">
        <font-awesome-icon icon="cog" size="2x" spin inverse @click="isChangingSettings = true" />
        <div class="quote-text loader"></div>
      </div>
    </div>
    <div v-else>
      <span style="display: flex; flex-direction: row; align-items: center;">
        <font-awesome-icon icon="cog" size="2x" spin inverse @click="isChangingSettings = true" />
        <p class="refresh-rate-text">{{ selectedRefreshOption.text }}</p>
      </span>
      <div class="author-text-container">
        <h6 class="author-text">[ {{ quote.author }} ]</h6>
      </div>
      <div class="quote-text-container">
        <h1 class="quote-text"><b>{{ quote.text }}</b></h1>
      </div>
    </div>
    <settings-modal v-if="isChangingSettings" @cancel="isChangingSettings = false" @confirm="settingsChanged" :refreshOption="selectedRefreshOption" />
  </div>
</template>

<script>
import SettingsModal from '@/components/SettingsModal'
import axios from 'axios'

export default {
  name: 'home',
  components: { SettingsModal },
  created () {
    this.retrieveQuoteCount()
      .then(response => {
        this.quoteIdMap = this.randomlyAllocatedArray(response.data.ItemCount)

        this.retrieveQuote()
        this.initializeInterval(this.retrieveQuote, this.selectedRefreshOption.value)
      })
  },
  data () {
    return {
      quote: undefined,
      quoteIdMap: undefined,
      quoteIdIndex: 0,
      intervalId: undefined,
      isChangingSettings: false,
      selectedRefreshOption: { text: 'refresh every 24 hours', value: 24.00 }
    }
  },
  methods: {
    retrieveQuote () {
      axios.post(`https://2zztwjimr6.execute-api.eu-west-1.amazonaws.com/prod/quote/${this.quoteIdMap[this.quoteIdIndex]}`, {}, this.config())
        .then(response => {
          this.safeIndexIncrement()
          this.quote = response.data.Item
        })
    },
    retrieveQuoteCount () {
      return axios.post('https://2zztwjimr6.execute-api.eu-west-1.amazonaws.com/prod/quote/count', {}, this.config())
    },
    initializeInterval (callback, refreshHours) {
      this.intervalId = setInterval(() => callback(), this.toMillis(refreshHours)) // hours
    },
    config () {
      return {
        headers: {
          'X-API-KEY': 'quvASESLkjh0F0xnsyJV4wFEFqOcIzP5zjbCiOk9'
        }
      }
    },
    randomlyAllocatedArray (totalCount) {
      let array = []
      let random

      for (let i = 0; i < totalCount; i++) {
        do {
          random = this.getRandomBetween(1, totalCount)
        } while (array.length > 0 && array.includes(random))

        array.push(random)
      }

      return array
    },
    getRandomBetween (from, to) {
      return Math.floor(Math.random() * to) + from
    },
    safeIndexIncrement () {
      this.quoteIdIndex++

      if (this.quoteIdIndex >= this.quoteIdMap.length) {
        this.quoteIdIndex = 0
      }
    },
    toMillis (hours) {
      return 1000 * 60 * 60 * hours
    },
    settingsChanged (newRefreshOption) {
      this.selectedRefreshOption = newRefreshOption
      clearInterval(this.intervalId)
      this.initializeInterval(this.retrieveQuote, newRefreshOption.value)

      this.isChangingSettings = false
    }
  }
}
</script>

